import render from '../render';
module.exports = function(node) {
  if (!node) {
    return '';
  }
  var nodeEl = render(node, this.cm, this.callback);
  var temp = document.createElement('div');
  temp.appendChild(nodeEl);
  return temp.innerHTML;
}



